import React, { useState } from "react";
import "../../style/Product/style.css";
import Cart from "../Cart/index";

const products = [
  {
    id: 1,
    name: "Furry Hooded",
    imageSrc: require("../../assets/images/shop/shop-1.jpg"),
    price: 590.0,
    rating: 5,
  },
  {
    id: 2,
    name: "Beautiful Bag",
    imageSrc: require("../../assets/images/shop/shop-2.jpg"),
    price: 390.99,
    rating: 4,
  },
  {
    id: 3,
    name: "Awesome Dress",
    imageSrc: require("../../assets/images/shop/shop-3.jpg"),
    price: 300.99,
    rating: 4,
  },
  {
    id: 4,
    name: "Stylish Jeans",
    imageSrc: require("../../assets/images/shop/shop-4.jpg"),
    price: 290.99,
    rating: 3,
  },
  {
    id: 5,
    name: "Cool Boots",
    imageSrc: require("../../assets/images/shop/shop-5.jpg"),
    price: 400.99,
    rating: 4,
  },
  {
    id: 6,
    name: "Fashionable Bag",
    imageSrc: require("../../assets/images/shop/shop-6.jpg"),
    price: 650.99,
    rating: 5,
  },
  {
    id: 7,
    name: "Amazing Ears Ring",
    imageSrc: require("../../assets/images/shop/shop-7.jpg"),
    price: 500.99,
    rating: 4,
  },
  {
    id: 8,
    name: "Trendy Scarf",
    imageSrc: require("../../assets/images/shop/shop-8.jpg"),
    price: 422.99,
    rating: 4,
  },
];

const Product = () => {
  const [cartItems, setCartItems] = useState([]);

  const addToCart = (product) => {
    setCartItems([...cartItems, product]);
  };

  const removeFromCart = (productToRemove) => {
    const updatedCartItems = cartItems.filter(
      (product) => product.id !== productToRemove.id
    );
    setCartItems(updatedCartItems);
  };

  const handleConfirmPurchase = () => {
    // Define your logic to confirm the purchase
    alert("Purchase process confirmed!"); // Display a confirmation message
  };

  return (
    <div>
      <div className="row">
        {products.map((product, index) => (
          <div className="col-lg-3 col-md-6" key={index}>
            <div className="product__item">
              <div className="product__item__pic">
                <img
                  className="product__item__pic"
                  src={product.imageSrc}
                  alt={product.name}
                />
                <ul className="product__hover">
                  <li>
                    <a href={product.imageSrc}>
                      <span className="fa fa-arrows-alt" />
                    </a>
                  </li>
                  <li>
                    <a href="#" onClick={() => addToCart(product)}>
                      <span className="fa fa-shopping-cart" />
                    </a>
                  </li>
                </ul>
              </div>
              <div className="product__item__text">
                <h6>
                  <a href="#">{product.name}</a>
                </h6>
                <div className="rating">
                  {Array.from({ length: product.rating }).map((_, i) => (
                    <i key={i} className="fa fa-star" />
                  ))}
                </div>
                <div className="product__price">${product.price}</div>
              </div>
            </div>
          </div>
        ))}
      </div>
      <Cart
        cartItems={cartItems}
        onRemoveItem={removeFromCart}
        onConfirmPurchase={handleConfirmPurchase}
      />
    </div>
  );
};

export default Product;
