// import React from "react";
import React, { useState } from "react";
import { BrowserRouter as Router, Route, Routes } from "react-router-dom";
import Home from "./components/Home/index";
import Women from "./components/Women/index";
import Men from "./components/Men/index";
import Kids from "./components/Kids/index";
import BestSeller from "./components/Bestseller/index";
import Footer from "./components/Footer/index";
import Header from "./components/Header/index";
import MainContainer from "./components/MainContainer/index";
import Login from "./components/Loogin/index";
import "./App.css";
import "bootstrap/dist/css/bootstrap.css";

function App() {
  const [isAuthenticated, setIsAuthenticated] = useState(false);

  const handleLogin = () => {
    setIsAuthenticated(true);
  };

  const handleLogout = () => {
    setIsAuthenticated(false);
  };

  return (
    <div className="App">
      <Router>
        {!isAuthenticated && (
          <Routes>
            <Route path="/" element={<Login onLogin={handleLogin} />} />
          </Routes>
        )}

        {isAuthenticated && (
          <>
            <Header isAuthenticated={isAuthenticated} onLogout={handleLogout} />
            <Routes>
              <Route path="/" element={<Home />} />
              <Route path="/women" element={<Women />} />
              <Route path="/men" element={<Men />} />
              <Route path="/kids" element={<Kids />} />
              <Route path="/bestseller" element={<BestSeller />} />
            </Routes>
            <MainContainer />
            <Footer />
          </>
        )}
      </Router>
    </div>
  );
}

export default App;
