import React, { useState } from "react";

const Cart = ({ cartItems, onRemoveItem, onConfirmPurchase }) => {
  const [isCartVisible, setIsCartVisible] = useState(true);
  const [isPurchaseConfirmed, setIsPurchaseConfirmed] = useState(false);

  const toggleCartVisibility = () => {
    setIsCartVisible(!isCartVisible);
  };

  const calculateTotalPrice = () => {
    let totalPrice = 0;
    for (const product of cartItems) {
      totalPrice += product.price;
    }
    return totalPrice;
  };

  const handleConfirmPurchase = () => {
    onConfirmPurchase();
    setIsPurchaseConfirmed(true);
  };

  return (
    <div>
      <h2>Shopping Cart</h2>
      <button className="btn btn-primary" onClick={toggleCartVisibility}>
        {isCartVisible ? "Hide Cart" : "Show Cart"}
      </button>
      {isCartVisible && (
        <div>
          <ul className="list-group">
            {cartItems.map((product, index) => (
              <li className="list-group-item" key={index}>
                <div className="cart-item">
                  <h5>{product.name}</h5>
                  <p>Price: ${product.price}</p>
                  <div>
                    <button
                      className="btn btn-danger"
                      onClick={() => onRemoveItem(product)}
                    >
                      Remove
                    </button>
                  </div>
                </div>
              </li>
            ))}
          </ul>
          <p>Total Price: ${calculateTotalPrice()}</p>
          <button className="btn btn-success" onClick={handleConfirmPurchase}>
            Confirm Purchase
          </button>
          {isPurchaseConfirmed && (
            <p>Purchase process confirmed. Thank you for shopping!</p>
          )}
        </div>
      )}
    </div>
  );
};

export default Cart;
